<template>
  <div>
    <el-progress stroke-width="24" :text-inside="true" :percentage="percentage" :status="stat"></el-progress>
    <el-input class="cookieInput" v-model="input" placeholder="请输入完整的B站Cookie"></el-input>
    <el-row>
      <el-button class="submitButton" type="primary" round :loading="isLoading" @click="handleClick()">开始执行</el-button>
    </el-row>
    <el-card class="box-card" shadow="hover">
      <div slot="header" class="clearfix" >
        <span>操作日志</span>
      </div>
      <div  v-for="item in log" :key="item"  class="text item">
      {{item}}
      </div>
    </el-card>
  </div>

</template>

<script>
import {biliTask} from '@/api'
export default {
  data() {
    return {
      active: 0,
      intervalId: null, // 用于存储setInterval返回的ID
      input:'',
      log:[],
      isLoading: false,
      percentage:0,
      stat:"success"
    };
  },
  methods: {
    handleClick(){
      this.stat = 'success'
      let dic={
        "totalCookie":this.input
      }
      this.log = this.log .concat( ['开始执行,totalCookie='+dic.totalCookie])
      this.isLoading = true
      this.percentage = 10
      biliTask(dic).then(data=>{
        this.percentage = 60
        if (data.data.flag){
          this.$message.success(data.data.message)
          this.log = this.log.concat(data.data.data.split("\n"))
        }
        else {
          this.$message.error(data.data.message)
          this.log = this.log.concat([data.data.message])
          this.stat = 'exception'
        }
        this.percentage = 90
        this.isLoading = false
        this.percentage = 100
      })
    }
  },
};
</script>
<style>
.cookieInput{
  margin-top: 20px;
  margin-bottom: 20px;
}
.submitButton{
  margin-bottom: 20px;
}
</style>